export const useGPTCompletionCheck = async (cardTitle, cardType, data, setGptText) => {
    let prompt = `
    JSON
    ${cardTitle}
    ${cardType}
    
    ITNG

    1.
    30-100:
    () %

    2.
    100%
    
    
    
    3.
    IT
    #JSON
    `;
    prompt += JSON.stringify(data);
    await callGPTAPIStream(prompt, setGptText);
}

