// GPT
const callGPTAPI = async (prompt) => {
    try {
        const endpoint = API_URL + "chat/completions";

        const data = {
            //  ID 
            model: MODEL,
            // 
            messages: [
                {
                    'role': 'user',
                    'content': prompt,
                }
            ],
        };

        const response = await axios.post(endpoint, data,{
            headers: {
                "Content-Type": "application/json",
                "Authorization": `Bearer ${process.env.REACT_APP_GPT_KEY}`,
            },
        });

        console.log(response);
        if (response.data.choices && response.data.choices.length > 0) {
            return response.data.choices[0].message.content;
        } else {
            throw new Error("GPT");
        }
    } catch (error) {
        console.error(error);
        throw new Error("GPT");
    }
};


// GPT
const callGPTAPIStream = async (prompt, setGptText) => {
    try {
        const endpoint = API_URL + "chat/completions";

        const data = {
            //  ID 
            model: MODEL,
            stream: true,

            // 
            messages: [
                {
                    'role': 'user',
                    'content': prompt,
                }
            ],
        };

        const response = await fetch(endpoint, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                Authorization: `Bearer ${process.env.REACT_APP_GPT_KEY}`,
            },
            body: JSON.stringify(data),
        });

        // ReadableStream 
        const reader = response.body?.getReader();

        if (response.status !== 200 || !reader) {
            throw new Error("GPT");
        }

        const decoder = new TextDecoder('utf-8');

        let sendMessage = ""
        const read = async () => {
            const { done, value } = await reader.read();
            if (done) return reader.releaseLock();

            const chunk = decoder.decode(value, { stream: true });
            //  chunk 
            // data: { ... }
            //  Event stream format 
            // https://developer.mozilla.org/en-US/docs/Web/API/Server-sent_events/Using_server-sent_events#event_stream_format
            console.log(chunk);

            const jsons = chunk
                //  split 
                .split('data:')
                // data  json parse 
                // [DONE] 
                .map((data) => {
                    const trimData = data.trim();
                    if (trimData === '') return undefined;
                    if (trimData === '[DONE]') return undefined;
                    return JSON.parse(data.trim());
                })
                .filter((data) => data);

            //  jsons 
            console.log(jsons);
            let chunkMessage = "";
            chunkMessage = jsons.map((data)=>{
                return data.choices[0].delta.content || "";
            })
            sendMessage += chunkMessage.join('');
            setGptText(sendMessage);

            return read();
        }
        await read();
    } catch (error) {
        console.error(error);
        throw new Error("GPT");
    }
};


