const addUuidsToJson = (data) => {
    return data.map(item => {
        return {
            ...item,
            id: uuidv4(),
            cards: item.cards.map(card => {
                return {
                    ...card,
                    id: uuidv4(),
                };
            }),
        };
    });
};


const addUuidsToCard = (data) => {
    return data[0].cards.map(item => {
        item.id = uuidv4()
        return item;
    });
};